<template>
  <div class="regist">
    <h1 class="underline">게시글 정보</h1>
    <div class="regist_form">
      <label for="bno">번호</label>
      <div class="view">{{ board.bno }}</div>
      <label for="title">제목</label>
      <div class="view">{{ board.title }}</div>
      <label for="author">작성자</label>
      <div class="view">{{ board.author }}</div>
      <label for="content">내용</label>
      <div class="view" v-html="enterToBr(board.content)"></div>
      <div style="padding-top: 15px">
        <router-link :to="`/board/modify/${board.bno}`" class="btn">수정</router-link>
        <a href="#" class="btn" @click="deleteBoard">삭제</a>
        <router-link to="/board" class="btn">목록</router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import http from "@/util/http-common";

export default {
  name: "viewdetail",
  computed: {
    ...mapGetters(["board"])
  },
  methods: {
    deleteBoard() {
      if (confirm("정말로 삭제하시겠습니까?")) {
        http.delete(`/${this.board.bno}`).then(({ data }) => {
          let msg = "삭제 처리시 문제가 발생했습니다.";
          if (data === "success") {
            msg = "삭제가 완료되었습니다.";
          }
          alert(msg);
          this.$router.push("/board");
        });
      }
    },
    numberWithCommas(x) {
      if (x) return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    enterToBr(str) {
      if (str) return str.replace(/(?:\r\n|\r|\n)/g, "<br />");
    }
  }
};
</script>

<style scoped>
.regist {
  padding: 10px;
}
.regist_form {
  text-align: left;
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 20px;
}
input,
textarea,
.view {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  color: #787878;
  font-size: medium;
}
</style>
